
html,body,div,span,td,tr {
  margin: 0;
  padding: 0;
}
.icon2 {
  width: 3em;
  height: 3em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
.bounce-enter-active {
  -webkit-animation: bounce-in .5s;
          animation: bounce-in .5s;
}
.bounce-leave-active {
  animation: bounce-in .5s reverse;
}
@-webkit-keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
div:focus {
  outline: blue solid 2px;
}
#game {
  position: absolute;
  width: 1200px;
  height: 800px;
  /* background-color: #42B983; */
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-image: url(data:image/jpeg;base64,);
  background-size: 100% 100%;
}
#gamemap {
  position: absolute;
  width: 500px;
  height: 500px;
  background-color: #bbada0;
  top: 50%;
  left: 25%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 20px;
}
.mapcell {
  position: absolute;
  width: 100px;
  height: 100px;
  background-color: #ccc0b3;
  margin: 20px 0 0 20px;
  border-radius: 10px;
  -webkit-transition: 200ms;
  transition: 200ms;
  z-index: -1;
  text-align: center;
  line-height: 100px;
  font-size: 3rem;
}
#message {
  position: absolute;
  /* background-color: #61C0ED; */
  width: 360px;
  height: 460px;
  border: solid 1px grey;
  padding: 20px;
  top: 50%;
  left: 70%;
  -webkit-transform: translate(-35%, -50%);
          transform: translate(-35%, -50%);
}
#explain{
  color: #F65E3B;
  opacity: .8;
}
#pause{
  width: 100%;
  height: 100%;
  background: rgba(0,0,0, .4);
  -webkit-transition: 500ms;
  transition: 500ms;
  line-height: 500px;
  text-align: center;
  font-size: 40px;
}
div:focus #pause {
  /* z-index: -2; */
  opacity: 0;
  visibility: hidden;
}
th {
  width: 20px;
}
.btn {
  background: transparent;
  border: 0;
  outline: none;
  /* outline: solid 1px gray; */
}

