header {
  background-color: white;
  position: relative;
  height: 2rem;
  width: 100%;
  line-height: 2rem;
  border-bottom: solid 1px #E0E0E0;
  z-index: 1;
  /* overflow: hidden; */
}
#head {
  position: absolute;
  left: 25%;
}
.nav {
  display: inline-block;
}
#search {
  left: 30%;
  height: 30px;
  border: solid 1px gray;
}
#search_input {
  /* background-color: #0086B3; */
  width: 300px;
  height: 22px;
  border: 0;
  padding: 0;
  outline-style: none;
}
.triangle::after{
  content: '';
  display: inline-block;
  position: relative;
  top: 5px;
  width: 0px;
  height: 0px;
  border-top: 7px solid gray;
  border-left: 7px solid transparent;
  border-right:7px solid transparent ;
  border-bottom: 7px solid transparent;
  /* background-color: #000000; */
}
#tools::before {
  content: '';
  display: inline-block;
  width: 150px;
}
#index:hover #index_data{
  /* display: block; */
  height: 300px;
}
#index:hover{
  background-color: #EEEEEE;
}
#history:hover #history_data{
  height: 300px;
}
#history:hover{
  background-color: #EEEEEE;
}
#lovely:hover #lovely_data{
  height: 300px;
}
#lovely:hover{
  background-color: #EEEEEE;
}
.nav_data{
  /* display: none; */
  position: absolute;
  background-color: #EEEEEE;
  width: 200px;
  height: 0px;
  padding: solid 1px gray ;
  overflow: hidden;
  -webkit-transition: 200ms;
  transition: 200ms;
}





